"use client";

import { useUser, RedirectToSignIn } from "@clerk/nextjs";
import Image from "next/image";
import { Clock, Heart, Star, Settings, LogOut } from "lucide-react";
import { SignOutButton } from "@clerk/nextjs";

export default function ProfilePage() {
    const { isLoaded, isSignedIn, user } = useUser();

    if (!isLoaded) return <div className="min-h-screen bg-bg-main flex items-center justify-center text-white">Loading...</div>;

    if (!isSignedIn) {
        return <RedirectToSignIn />;
    }

    return (
        <main className="min-h-screen pt-24 pb-20 bg-bg-main">
            <div className="container mx-auto px-6 lg:px-20">
                {/* Header */}
                <div className="flex flex-col md:flex-row items-center gap-8 mb-12 bg-bg-card p-8 rounded-2xl border border-white/5 relative overflow-hidden">
                    {/* Background Glow */}
                    <div className="absolute top-0 right-0 w-96 h-96 bg-accent-primary/10 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2" />

                    <div className="relative">
                        <div className="w-32 h-32 rounded-full border-4 border-bg-main overflow-hidden ring-4 ring-white/10">
                            <Image
                                src={user.imageUrl}
                                alt={user.fullName || "User"}
                                fill
                                className="object-cover"
                            />
                        </div>
                    </div>

                    <div className="flex-1 text-center md:text-left relative z-10">
                        <h1 className="text-3xl font-display font-bold text-white mb-2">{user.fullName}</h1>
                        <p className="text-text-secondary mb-4">{user.primaryEmailAddress?.emailAddress}</p>
                        <div className="flex flex-wrap gap-3 justify-center md:justify-start">
                            <div className="px-4 py-2 rounded-full bg-white/5 border border-white/10 text-sm text-text-muted flex items-center gap-2">
                                <Heart size={14} className="text-accent-primary" /> 12 Loved
                            </div>
                            <div className="px-4 py-2 rounded-full bg-white/5 border border-white/10 text-sm text-text-muted flex items-center gap-2">
                                <Clock size={14} className="text-accent-secondary" /> 45 Watched
                            </div>
                        </div>
                    </div>

                    <div className="flex gap-4 relative z-10">
                        <button className="p-3 rounded-full bg-white/5 hover:bg-white/10 border border-white/10 text-white transition-colors">
                            <Settings size={20} />
                        </button>
                        <SignOutButton>
                            <button className="p-3 rounded-full bg-red-500/10 hover:bg-red-500/20 border border-red-500/20 text-red-500 transition-colors">
                                <LogOut size={20} />
                            </button>
                        </SignOutButton>
                    </div>
                </div>

                {/* Content Tabs */}
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    {/* Watchlist */}
                    <div className="lg:col-span-2 space-y-6">
                        <h2 className="text-xl font-bold text-white flex items-center gap-2">
                            <span className="w-1 h-6 bg-accent-primary rounded-full" />
                            My Watchlist
                        </h2>
                        {/* Placeholder for Watchlist Items */}
                        <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                            {[1, 2, 3].map((i) => (
                                <div key={i} className="aspect-video bg-white/5 rounded-xl border border-white/5 flex items-center justify-center text-text-muted text-sm">
                                    Display Movie {i}
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* Recently Watched */}
                    <div className="space-y-6">
                        <h2 className="text-xl font-bold text-white flex items-center gap-2">
                            <span className="w-1 h-6 bg-accent-secondary rounded-full" />
                            History
                        </h2>
                        <div className="space-y-4">
                            {[1, 2, 3, 4].map((i) => (
                                <div key={i} className="flex gap-4 p-3 rounded-xl bg-white/5 hover:bg-white/10 transition-colors cursor-pointer border border-transparent hover:border-white/5">
                                    <div className="w-12 h-16 bg-black rounded shadow-sm shrink-0" />
                                    <div>
                                        <p className="text-white font-medium text-sm">Watched Movie {i}</p>
                                        <p className="text-xs text-text-muted mt-1">2 days ago</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}
